  height: auto;
  line-height: 1.5;
}

.reservationDetail .btn {
  width: 100%;
}

.reservationDetail__booking {
  line-height: 1.5;
}

.reservationDetail__booking__toggle {
  position: absolute;
  opacity: 0;
}

.reservationDetail__booking__toggle:checked ~ label {
  border-bottom: 1px solid #fff;
}

.reservationDetail__booking__toggle:checked ~ label > div:first-child {
  visibility: hidden;
}

.reservationDetail__booking__toggle:checked ~ label .material-icons {
  transform: rotate(180deg);
}

.reservationDetail__booking__toggle:checked ~ .collapse {
  display: block;
}

.reservationDetail__booking .collapse {
  position: absolute;
  top: 57px;
  left: 0;
  right: 0;
}

@media (min-width: 768px) {
  .reservationDetail__booking .collapse {
    position: relative;
    top: 0;
  }
}

